<template>
    <div class="phpdoc-entity" v-if="entity">

        <header class="phpdoc-header">
            <i :class="'phpdoc-type-' + file.type"></i>
            <h3 class="header-title">
                <span :class="'phpdoc-type-' + file.type">{{ entity.full_name | removeStartSlash}}</span>
                <small v-if="hasExtend" class="pl-xs fs-13">extends</small>
                <pd-type v-if="hasExtend" :type="entity.extends" class="fs-13"></pd-type>
            </h3>
        </header>

        <!-- DESC-->
        <div class="phpdoc-description">
            <p v-if="hasDescription" class="fs-13">{{ entity.description }}</p>
            <pd-tags :object="entity" :exclude="['example', 'inherited_from']"></pd-tags>
        </div>

        <c-popover popover-class="popover-phpdoc">
            <a class="btn btn-secondary btn-sm">Methods Settings</a>
            <span slot="content">
                <pd-list-settings list="methodList"></pd-list-settings>
            </span>
        </c-popover>

        <!-- TABS: METHODS, PROPERTIES, SOURCE-->
        <div class="tabbable phpdoc-tabs" id="phpdoc-tabs">
            <ul role="tablist" class="nav nav-tabs phpdoc-tabs-nav">
                <li role="presentation" class="nav-item" :class="{ 'active' : isActive('methods') }"><a href="javascript:;" class="nav-link" @click.prevent="setActive('methods')" aria-controls="phpdoc-methods" role="tab">Methods</a></li>
                <!--<li role="presentation" class="nav-item" :class="['tab-filters-toggler', { 'active' : isActiveFiltersPane('methods') }]"><a href="javascript:;" class="nav-link" @click.prevent="toggleFiltersPane('methods')" role="tab"><i class="fa fa-cog"></i></a></li>-->
                <li role="presentation" class="nav-item" :class="{ 'active' : isActive('properties') }"><a href="javascript:;" class="nav-link" @click.prevent="setActive('properties')" aria-controls="phpdoc-properties" role="tab">Properties</a></li>
                <!--<li role="presentation" class="nav-item" :class="['tab-filters-toggler', { 'active' : isActiveFiltersPane('properties') }]"><a href="javascript:;" class="nav-link" @click.prevent="toggleFiltersPane('properties')" role="tab"><i class="fa fa-cog"></i></a></li>-->
                <li role="presentation" class="nav-item" :class="{ 'active' : isActive('source') }"><a href="javascript:;" class="nav-link" @click.prevent="setActive('source')" aria-controls="phpdoc-source" role="tab">Source</a></li>
            </ul>
            <div class="tab-content" ref="tabContent" :style="{ 'max-height': maxHeight ? maxHeight : 0 }">

                <div role="tabpanel" class="tab-pane phpdoc-tab-methods" :class="{ 'active' : isActive('methods') }">
                    <div class="tabbable tabs-left">
                        <pd-method-list
                                ref="methodList"
                                :full-name="entity.full_name"
                                use-signature
                                signature-hide-modifiers
                                signature-hide-argument-types
                                signature-hide-argument-defaults
                                signature-hide-argument-return
                                signature-hide-return
                                selectable
                                v-on:select="onMethodSelect"
                        ></pd-method-list>
                    </div>
                </div>

                <div role="tabpanel" class="tab-pane phpdoc-tab-properties" :class="{ 'active' : isActive('properties') }">
                    <div class="tabbable tabs-left">
                        <pd-property-list :full-name="entity.full_name" selectable v-on:select="onPropertySelect"></pd-property-list>
                    </div>
                </div>


                <div role="tabpanel" class="tab-pane phpdoc-tab-source" :class="{ 'active' : isActive('source') }">

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import 'jquery-slimscroll'
    export default {
        name    : 'pd-entity',
        props   : {
            fullName : String,
            maxHeight: {type: Number, default: 0}
        },
        data(){
            return {
                fileData        : false,
                active          : 'methods',
                selectedProperty: false,
                selectedMethod  : false,
                hasSlimScroll   : false
            }
        },
        mounted(){
            if ( this.fullName ) this.setEntity(this.fullName);
            this.$$ready(() => this.applySlimScroll());
        },
        watch   : {
            fullName(newVal){
                this.setEntity(newVal);
            },
            maxHeight(val){
                this.applySlimScroll();
//                if ( typeof val !== 'number' || val === 0 ) {
//                    return this.destroySlimScroll();
//                }
//                this.applySlimScroll(val);
            }
        },
        computed: {
            ...mapGetters({_file: 'file'}),
            methodSettingsList() {
                return this.$refs.methodList;
            },
            file(){
                return this._file ? this._file : this.fileData;
            },
            entity()            {
                return this.file ? this.file.entity : false;
            },
            hasExtend()            {
                return this.entity.extends && this.entity.extends.length > 0;
            },
            hasDescription()            {
                return this.entity.description.length > 0;
            },
        },
        methods : {

            applySlimScroll(){
                if ( this.maxHeight === 0 ) return;
                this.$nextTick(() => {

                    this.$$(this.$refs.tabContent).slimScroll({
                        height       : this.maxHeight + 'px',
                        alwaysVisible: false
                    });

                    console.log('applySlimScroll done', this.maxHeight, this);
                });

            },
            destroySlimScroll(){
                if ( this.hasSlimScroll !== true ) return;
                this.$$(this.$refs.tabContent).slimScroll({destroy: true})
                this.hasSlimScroll = false;
            },
            onPropertySelect(propertyName){
                this.selectedProperty = propertyName;
            },
            onMethodSelect(methodName){
                this.selectedMethod = methodName;
            },
            setEntity(entity){
                console.log('setEntity mounted');
                if ( this.file ) {

                } else {
                    this.$phpdoc.getEntity(entity).then(data => this.fileData = data);
                }
            },
            setActive(name){
                this.active = name;
                this.$emit('open', name);
                this.$root.$emit('resize')
            },
            isActive(name){
                return this.active === name;
            }
        }
    }

</script>




