<template>
    <div class="phpdoc-method-signature" v-if="methodName !== false">
        <span v-show="show.modifiers" v-if="method.static" class="phpdoc-modifier-static">static</span>
        <span v-show="show.modifiers" v-if="method.abstract" class="phpdoc-modifier-abstract">abstract</span>
        <span v-show="show.modifiers" :class="['phpdoc-visibility-' + method.visibility]">{{ method.visibility }}</span>
        &nbsp;{{ method.name }}&nbsp;
        <strong>(</strong>
        <span v-show="show.arguments" v-for="(argument, argumentIndex) in method.arguments">
            <strong v-if="argumentIndex !== 0">,</strong>
            <pd-type v-show="show.argumentTypes" :type="argument.types" :no-tooltip="noTooltip"></pd-type>
            <span class="color-cyan-900">{{ argument.name }}</span>
            <span v-show="show.argumentDefaults" class="color-grey-700 fs-10" v-if="argument.default.length > 0"> = {{ argument.default }}</span>
        </span>
        <strong>)</strong>
        <span v-show="show.return" v-if="method.returns && method.returns.length > 0">{{ returnCharacter }}</span>
        <pd-type v-show="show.return" v-if="method.returns" :type="method.returns" :no-tooltip="noTooltip"></pd-type>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import {find} from 'lodash'
    import {filter} from 'lodash'
    import {parseFullName} from 'phpdoc/utils'

    export default {
        name    : 'pd-method-signature',
        props   : {
            // ex: \Codex\Codex::get()
            fullName            : String,
            hideModifiers       : Boolean,
            hideReturn          : Boolean,
            hideArguments       : Boolean,
            hideArgumentTypes   : Boolean,
            hideArgumentDefaults: Boolean,
            noTooltip          : Boolean,
            returnCharacter     : {type: String, default: '=>'}
        },
        data(){
            return {method: false}
        },
        mounted(){
            if ( this.fullName ) this.setMethod(this.fullName);
        },
        watch   : {
            fullName(newVal){
                this.setMethod(newVal);
            }
        },
        methods : {
            setMethod(fullName){
                if ( this.file ) {
                    this.method = find(this.file.entity.methods, {'full_name': fullName});
                } else {
                    let {entity, method} = parseFullName(fullName);
                    return this.$phpdoc.getMethod(entity, method).then(method => this.method = method);
                }
            }
        },
        computed: {
            ...mapGetters(['file']),
            hasReturn(){
                return this.methodName.returns.length > 0;
            },
            show(){
                return {
                    'modifiers'       : ! this.hideModifiers,
                    'return'          : ! this.hideReturn,
                    'arguments'       : ! this.hideArguments,
                    'argumentTypes'   : ! this.hideArgumentTypes,
                    'argumentDefaults': ! this.hideArgumentDefaults,
                }
            }
        }
    }
</script>


