<template>
    <!--Methods List-->
    <ul class="phpdoc-method-list" v-if="entity">
        <li v-for="method in filteredMethods" :class="liClass.concat(active === method.name ? ['active']:[])">

            <a @click.prevent.stop="onMethodClick(method)" href="javascript:;" :class="aClass">
                <i v-show="!hideIcon" class="pr-xs" :class="'phpdoc-visibility-' + method.visibility"></i>
                <span v-if="useSignature === false">{{ method.name }}</span>
                <pd-method-signature
                        v-if="useSignature"
                        :full-name="method.full_name"
                        :hide-modifiers="signatureHideModifiers"
                        :hide-argument-types="signatureHideArgumentTypes"
                        :hide-argument-defaults="signatureHideArgumentDefaults"
                        :hide-return="signatureHideReturn"
                ></pd-method-signature>
            </a>

            <a v-if="method.inherited"
               @click.prevent.stop="$emit('inheritedClick', method)"
               class="nav-link nav-link-modifier"
               href="javascript:;">
                <c-popover popover-class='popover-phpdoc' trigger='hover' placement="right" effect="fade">
                    <i class="phpdoc-modifier-inherited"></i>
                    <span slot="content">
                        Inherited from: <br>
                        <pd-type :type="method.full_name" full-name no-tooltip></pd-type>
                    </span>
                </c-popover>
            </a>

        </li>
    </ul>
</template>

<script>
    import Vue from 'vue';
    import {mapGetters, mapActions} from 'vuex';
    import {chain, find} from 'lodash';
    import {entityChild} from 'phpdoc/mixins'


    export default {
        name    : 'pd-method-list',
        mixins  : [entityChild],
        props   : {
            itemClass                    : {type: String, default: 'nav-item'},
            linkClass                    : {type: String, default: 'nav-link'},
            exclude                      : {type: Array, default: () => []},
            hide                         : {type: String, default: ''},
            selectable                   : {type: Boolean, default: false},
            hideIcon                     : Boolean,
            useSignature                 : Boolean,
            signatureHideModifiers       : Boolean,
            signatureHideArgumentTypes   : Boolean,
            signatureHideArgumentDefaults: Boolean,
            signatureHideReturn          : Boolean,
            persistSettings              : Boolean,
            mergeSettings                : Object
        },
        data(){
            return {
                active     : undefined,
                settingsKey: 'pd-method-list-setting',
                settings   : {
                    show: {
                        static   : true,
                        abstract : true,
                        inherited: false,
                        final    : true,
                        public   : true,
                        protected: true,
                        private  : true
                    },
                    sort: {
                        by : '',
                        dir: 'asc'
                    },
                    view: ''
                }
            }
        },
        mounted(){
            console.log('method list', this)
            this.loadSettings();
            if ( this.mergeSettings ) {
                this.recursiveSetter(this.settings, this.mergeSettings);
            }
        },
        watch   : {
            settings: {
                deep: true,
                handler(val, old){
                    this.saveSettings(val);
                }
            }
        },
        computed: {
            methods(){
                return this.entity.methods;
            },
            filteredMethods(){
                return this.filter(this.methods)
            },
            liClass(){
                return this.itemClass.split(' ');
            },
            aClass(){
                return this.linkClass.split(' ');
            }
        },
        methods : {
            saveSettings(settings){
                if ( ! this.persistSettings ) return;
                this.$cookie.set(this.settingsKey, JSON.stringify(settings || this.settings))
            },
            loadSettings(){
                if ( ! this.persistSettings ) return;
                let parsed = JSON.parse(this.$cookie.get(this.settingsKey))
                if ( parsed !== null ) {
                    console.log('loadPersistance', parsed);
                    this.recursiveSetter(this.settings, parsed);
                }
            },
            // make sure only to overide keys that exist, not to add in bad keys after version changes
            recursiveSetter(current, loaded){
                Object.keys(current).forEach(key => {
                    if ( typeof current[key] === 'object' ) {
                        if ( typeof loaded[key] !== 'undefined' ) {
                            console.log('recursiveSetter going into', key);
                            this.recursiveSetter(current[key], loaded[key]);
                        }
                    } else {
                        if ( typeof loaded[key] !== 'undefined' ) {
                            current[key] = loaded[key];
                            console.log('recursiveSetter', key, 'from', current[key], 'to', loaded[key]);
                        }

                    }
                })
            },
            filter(methods) {
                let items = chain(methods);

                if ( this.settings.show.inherited === false ) {
                    items = items.filter((item) => item.inherited !== true);
                }
                if ( this.settings.show.public === false ) {
                    items = items.filter((item) => item.visibility !== 'public');
                }
                if ( this.settings.show.protected === false ) {
                    items = items.filter((item) => item.visibility !== 'protected');
                }
                if ( this.settings.show.private === false ) {
                    items = items.filter((item) => item.visibility !== 'private');
                }

                items = items.sortBy(this.settings.sort.by);

                if ( this.settings.sort.dir === 'desc' ) {
                    items = items.reverse();
                }

                return items.value();
            },
            setActive(methodName){
                if ( ! this.selectable ) return;
                this.active = methodName;
                this.$emit('select', methodName)
            },
            isActiveProperty(method){
                if ( ! this.selectable ) return false;
                return this.method === method;
            },
            onMethodClick(method){
                this.$emit('click', method)
                this.setActive(method.name);
            }
        }
    }
</script>


