<template>
    <div class="phpdoc-tags">
        <table v-if="filteredTags.length > 0" class="table table-hover table-bordered table-tags">
            <tbody>
            <tr v-for="tag in filteredTags">
                <th width="150" valign="middle">{{ tag.name }}</th>
                <td><span v-if="tag.description.length > 0" v-html="tag.description"></span></td>
                <td>
                    <pd-type v-if="tag.type" :type="tag.type"></pd-type>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    import {FullyQualifiedNameParser} from 'phpdoc/utils'
    import {filter} from 'lodash'

    /**
     * Tags can be found on Entities, Methods and Properties.
     * We use a Fully Qualified Name as property to indicate where we want to look for tags
     */
    export default {
        name    : 'pd-tags',
        props   : {
            fqn    : String,
            exclude: {type: Array, default: []}
        },
        data(){
            return {res: false, tags: []}
        },
        mounted(){
            if ( this.fqn ) this.setFQN(this.fqn);
        },
        watch   : {
            fqn(newVal){
                this.setFQN(newVal);
            }
        },
        computed: {
            isEntity(){
                return this.res.isEntity()
            },
            isMethod(){
                return this.res.isMethod()
            },
            isProperty(){
                return this.res.isProperty()
            },
            filteredTags(){
                if ( this.tags.length === 0 ) return [];
                return filter(this.tags, (tag) => {
                    return this.exclude.indexOf(tag.name) === - 1;
                });
            }
        },
        methods : {
            setFQN(fqn){
                this.res = new FullyQualifiedNameParser(fqn);
                this.res.requestApi().then(data => {
                    console.log('tags requestApi data', data);
                    this.tags = data.tags;
                })
            }
        }
    }
</script>




