<template>
    <div class="phpdoc-list-settings">
        <form class="form-horizontal">
            <div class="row">
                <div class="col-md-3">
                    <h4>Filters</h4>
                    <div class="pretty success smooth"><input type="checkbox" v-model="inherited"> <label><i class="fa fa-check"></i> Show inherited {{ name }}</label></div>
                    <div class="pretty success smooth"><input type="checkbox" v-model="public"> <label><i class="fa fa-check"></i> Show public {{ name }}</label></div>
                    <div class="pretty success smooth"><input type="checkbox" v-model="protected"> <label><i class="fa fa-check"></i> Show protected {{ name }}</label></div>
                    <div class="pretty success smooth"><input type="checkbox" v-model="private"> <label><i class="fa fa-check"></i> Show private {{ name }}</label></div>
                </div>
                <div class="col-md-3">
                    <h4>Sorting</h4>
                    <div class="form-group">
                        <label for="sort-by" class="col-sm-2 hide control-label">Sort By</label>
                        <div class="col-sm-12">
                            <select class="form-control" id="sort-by" v-model="sortBy">
                                <option v-for="option in sortMethodsOptions">{{ option }}</option>
                            </select>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="radio col-sm-6">
                            <label> <input type="radio" value="asc" v-model="sortDir"> Ascending </label>
                        </div>
                        <div class="radio col-sm-6">
                            <label> <input type="radio" value="desc" v-model="sortDir"> Descending </label>
                        </div>
                    </div>
                </div>
                <div class="col-md-2 col-md-offset-1">
                    <h4>&nbsp;</h4>
                    <a href="#" class="pull-right btn btn-primary btn-block" @click.prevent="closeFilters()">Close</a>
                    <a href="#" class="pull-right btn btn-primary btn-block" @click.prevent="resetFilters()">Reset to default</a>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import {find} from 'lodash'
    import {filter} from 'lodash'

    export default {
        name    : 'pd-list-settings',
        props   : {
            type: String,
            list: String
        },
        data(){
            return {
                sortMethodsOptions: ['name', 'visibility']
            }
        },
        mounted(){
            
            console.log('beforeMounted list-settings', this.lists);
        },
        methods : {
            closeFilters(){
                this.$eventHub.$emit('phpdoc.filters.close');
            }

        },
        computed: {
            settings: {
                get(){
                    return this.$helpers.getRefInParents(this.list, this.$parent).settings;
                }
            },
            inherited: {
                get(){
                    return this.settings ? this.settings.show.inherited : false;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {inherited: v}}});
                },
            },
            public   : {
                get(){
                    return this.settings ? this.settings.show.public : false;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {public: v}}});
                },
            },
            protected: {
                get(){
                    return this.settings ? this.settings.show.protected : false;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {protected: v}}});
                },
            },
            private  : {
                get(){
                    return this.settings ? this.settings.show.private : false;
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {show: {private: v}}});
                },
            },
            sortBy   : {
                get(){
                    return this.settings ? this.settings.sort.by : '';
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {sort: {by: v}}});
                },
            },
            sortDir  : {
                get(){
                    return this.settings ? this.settings.sort.dir : 'asc'
                },
                set(v){
                    this.updatePhpdocSettings({type: this.type, settings: {sort: {dir: v}}});
                },

            }
        }
    }
</script>


